.student-stats-container {
  .list-group-item.topic-error-item {
    padding: 0.25rem 1.25rem;
  }

  .topic-errors-chart-container {
    width: 2em;
  }

  .exam-stat-item .image-container {
    width: 50px;
    min-width: 50px;
  }

  .exam-list-group-item.list-group-item.custom-list-group-item:hover {
    color: inherit;
    background-color: inherit;
  }

  svg.pie-chart {
    width: 2em;
    border-radius: 50%;
    transform: rotate(-90deg);

    circle {
      fill: none;
      stroke-width: 16;
      r: 16;
      cx: 16;
      cy: 16;

      &.bg {
        stroke: var(--secondary);
      }

      &.fg {
        stroke: var(--success);
      }
    }
  }

  .explanation-icon {
    width: 24px;
    cursor: pointer;
  }
}
