  font-size: 16px;
  position: absolute;
  left: 160px;
  width: 1200px;
  overflow: hidden;
  z-index: 1;
  color: white;
}

.slide-enter {
  transform: translateX(0);
  opacity: 1;
  position: absolute;
  z-index: 5;
}

.slide-enter.slide-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: all 300ms linear 300ms;
  z-index: 5;
}

.slide-exit {
  transform: translateX(0);
  opacity: 1;
  z-index: 5;
}

.slide-exit.slide-exit-active {
  transform: translateX(100vw);
  opacity: 1;
  transition: all 500ms linear;
  z-index: 5;
}

